.prod-page {
	&__top {
		@include clr;
		margin-bottom: 55px; }
	&__slider-side {
		width: percentage(360 / 1140);
		float: left;
		padding-top: 8px; }
	&__desrc-side {
		width: percentage(652 / 1140);
		float: right; }
	&__help-block {
		font-size: 24px;
		p {
			margin-bottom: 5px; }
		a {
			font-size: 30px;
			color: $blue;
			@include rob-m; } }
	&__title {
		text-align: left;
		margin-bottom: 5px; }
	&__two-col {
		margin-bottom: 80px; } }
.prod-main-slider {
	margin-bottom: 19px;
	font-size: 0;
	&__slide {
		height: 283px;
		img {
			object-fit: cover;
			width: 100%;
			height: 100%; } } }
.prod-nav-slider {
	font-size: 0;
	margin-bottom: 60px;
	&__slide {
		width: 67px;
		height: 64px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	.slick-slide {
		@include trans;
		width: 67px!important;
		margin: 0 15px;
		cursor: pointer; } }
.prod-rating {
	margin-bottom: 50px;
	font-size: 0;
	&__count, &__text {
		display: inline-block;
		vertical-align: top; }
	&__count {
		@include rob;
		font-size: 18px;
		margin-right: 71px;
		padding-top: 2px; }
	&__text {
		@include robc-b;
		font-size: 24px;
		color: $green;
		padding-bottom: 2px; }
	.star-ratings {
		margin-right: 10px; } }
.prod-form {
	max-width: 555px;
	&__text {
		margin-bottom: 17px; }
	&__line {
		font-size: 0;
		padding-top: 6px;
		&--btn {
			text-align: right; }
		.prod-form__block {
			&:last-of-type {
				margin-right: 0; } } }
	&__block {
		width: 164px;
		display: inline-block;
		vertical-align: top;
		margin-right: 30px;
		margin-bottom: 33px;
		label {
			display: block;
			height: 55px;
			background-color: $white;
			border: 1px solid $blue-d;
			@include trans;
			color: $blue;
			@include rob-m;
			text-align: center;
			cursor: pointer;
			@include vertical;
			&:hover {
				background: $blue-d;
				color: $white; } }
		span {
			display: inline-block;
			vertical-align: middle;
			font-size: 20px; }
		input {
			display: none;
			&:checked + label {
				background: $blue-d;
				color: $white; } } }
	&__price {
		min-height: 75px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		padding: 10px 5px;
		border-left: 1px solid $blue;
		border-right: 1px solid $blue;
		border-bottom: 1px solid $blue;
		color: $blue;
		font-size: 20px;
		p {
			color: $green; } } }
.prod-lists {
	padding-top: 66px;
	.list {
		margin-bottom: 58px; } }
.prod-questions {
	margin-bottom: 51px;
	&__inner {
		padding-left: 98px; } }
.prod-question {
	margin-bottom: 28px;
	&__inner {
		@include clr; }
	&__title {
		margin-bottom: 28px;
		padding-top: 10px; }
	&__text {
		p {
			margin-bottom: 15px; } }
	img {
		max-width: 262px;
		max-height: 328px;
		height: auto;
		float: right;
		margin-bottom: 10px;
		margin-left: 30px; }
	&--img-left {
		img {
			float: left;
			margin-left: 0;
			margin-right: 30px; } } }
